<template>
  <div class="search">
    <input maxlength="16" @focus="onFocus" @blur="onBlur" v-model="inputVal" autocomplete="off" placeholder="..." name="s" class="input" type="text">
    <button type="submit" class="submit" @click="SearchWord"></button>
    <transition name="el-fade-in-linear">
        <div v-show="show" class="transition-box">
          <!-- <ul>
            <li @click="handle(item.id)" :key="index" v-for="(item,index) in dataArr">{{item.title}}</li>
          </ul> -->
          <SearchItem  
            :key="index" 
            v-for="(item,index) in dataArr"
            :id="item.id" 
            :index="index" 
            :title="item.title"
            :like_count="item.like_count"
            @click.native="handle(item.id)"
          />
        </div>
    </transition>
  </div>
</template>

<script>
import SearchItem from  './searchItem.vue'
  export default {
    data(){
      return {
        inputVal:null,
        show: false,
        dataArr:[]
      }
    },
    components:{
      SearchItem
    },
    methods:{
      SearchWord(){
        console.log("inputVal",this.inputVal)
      },
      onFocus(){
        console.log("");
        this.show = !this.show
      },
      onBlur(){
        console.log("");
        this.show = !this.show
      },
      handle(id){
      console.log("id",id);
      this.$router.push({path:'detail/'+id})
      },
      handleDataType(arr){

        for(let i = 0; i < arr.length - 1; i++){
          for(let j = 0; j < arr.length - i - 1; j++){
              let temp = null;
            if(arr[j].like_count > arr[j + 1].like_count){
                temp = arr[j];
                arr[j] = arr[j + 1];
                arr[j + 1] = temp;
            }
          }
        }
        return arr.reverse()
      },
      async getTypeList (){
       let res  = await this.$http.get('/api/article/typeList');
       this.dataArr = this.handleDataType(res.data.data).slice(0,6);
       console.log(this.dataArr);
      }

    },
    created(){
      this.getTypeList()
    }
  }
</script>

<style lang="scss" scoped>

.search .input {
  width: 170px;
  height: 34px;
  border-radius: 17px 0 0 17px;
  padding-left: 15px;
  outline:none;
  background: rgb(235, 233, 233);
  
  border:1px solid transparent;
  position: relative;
}
.input:focus {
  background:rgb(255, 255, 255);
  border:1px solid #409eff;
}
button {
  
  height: 38px;
  transform: translateY(1px);
  border: none;
  color: #fff;
  border-radius: 0 17px 17px 0;
  padding: 0 10px;
  background: #409eff;
  cursor:pointer;
}

/*  */
 .transition-box {
  box-shadow: 0 0 10px rgb(224, 217, 217);
  margin-bottom: 10px;
  width: 235px;
  border-radius: 4px;
  background-color: #fff;
  text-align: center;
  border-radius: 5px;
  box-sizing: border-box;
  margin-right: 20px;
  position: absolute;
  top: 60px;
  right: 45px;
  margin-left: auto;
  margin-right: auto;
  }

</style>