// head
<template>
<div>
  <!-- pc -->
  <header>
      <div class="wrapper">
        <el-row>
          <el-col :span="4">
            <div class="logo threed"></div>
          </el-col>
          <!--  -->
          <el-col :span="20">
            <!--  -->
            <el-menu mode="horizontal" 
            :default-active="activeIndex" 
            @select="handleSelect"
            background-color="#2d2d2d"
            text-color="#9d9d9d"
            active-text-color="#fff"
            class="nav"
            >
              <el-menu-item index="1" @click="goToto">
                 <router-link to="/" ><i class="iconfont My-new-iconshouye"></i></router-link>
              </el-menu-item>
              <el-menu-item index="2" @click="goToto">
                 <router-link to="/Lists"><i class="iconfont My-new-iconzixun"></i></router-link>
              </el-menu-item>
              <el-menu-item index="3" @click="goToto">
                  <router-link to="/timeLocus" ><i class="iconfont My-new-icondaojishi"></i></router-link>
              </el-menu-item>
              <el-menu-item index="4" @click="goToto">
                 <router-link to="/recommend" ><i class="iconfont My-new-icondianzan"></i></router-link>
              </el-menu-item>
              <el-menu-item index="5" @click="goToto">
                 <router-link to="/aboutMe" ><i class="iconfont My-new-iconwode1"></i></router-link>
              </el-menu-item>
              <el-menu-item index="6" @click="goToto">
                 <router-link to="/LeaveWord"><i class="iconfont My-new-iconbianji"></i></router-link>
              </el-menu-item>
              <el-menu-item index="8"  @click="goToto">
                 <router-link to="/article" v-if="isSignIn === 1 && nickname === ''"></router-link>
              </el-menu-item>
              <el-menu-item index="9" @click="goToto">
                 <router-link to="/login"  v-if="isSignIn === 0" class="login"><i class="iconfont My-new-iconxuanzhonghaoyou"></i></router-link>
              </el-menu-item>
               <el-menu-item index="10"  @click="goToto">
                 <router-link to="/personal"  v-if="isSignIn === 1 "  class="login">{{UserInfo.nickname === '' ? '' + UserInfo.nickname : ''+ UserInfo.nickname}}</router-link>
              </el-menu-item>
            </el-menu>
          </el-col>
          <!--  -->
          <!-- <el-col :span="6">
            <Search/>
          </el-col> -->
        </el-row>
      </div>
  </header>
  <div id="mobileNav">
    <!--  -->
      <el-row class="tac">
      <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          router="true">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span class="logotit"></span>
              <span v-if="UserInfo.nickname">{{UserInfo.nickname}},</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/" @click="Goto"><i class="iconfont My-new-iconshouye"></i></el-menu-item>
              <el-menu-item index="/Lists" @click="Goto"><i class="iconfont My-new-iconzixun"></i></el-menu-item>
               <el-menu-item index="/timeLocus" @click="Goto"><i class="iconfont My-new-icondaojishi"></i></el-menu-item>
              <el-menu-item index="/recommend" @click="Goto"><i class="iconfont My-new-icondianzan"></i></el-menu-item>
              <el-menu-item index="/aboutMe" @click="Goto"><i class="iconfont My-new-iconwode1"></i></el-menu-item>
               <el-menu-item  index="/LeaveWord" @click="Goto"><i class="iconfont My-new-iconbianji"></i></el-menu-item>
              <el-menu-item index="/personal" v-if="UserInfo.nickname" @click="Goto"><i class="iconfont My-new-iconwode1"></i>{{UserInfo.nickname}}</el-menu-item>
              <el-menu-item  index="/login" v-else @click="Goto"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-row>
  </div>
  
</div>
    
</template>

<script>
import Search from './search/index.vue'
  export default {
    data() {
      return {
        activeIndex:'1',
        UserInfo:{},

        nickname:''
      }
    },
    components:{
      Search
    },
    methods: {
      // 
        handleSelect(index) {
          this.activeIndex = index + ''
        },

      goToto(){
        console.log(11);
        this.$emit('goTo')
      },
      
      // 
      GetInfo(){
         this.$http.get('/api/users/info').then( (res) => {
           console.log(res);
           this.UserInfo = res.data.data
           this.nickname = res.data.data.nickname
           let avatar = res.data.data.head_img
          //   sessionStorage
          sessionStorage.setItem("avatar", avatar);

           sessionStorage.setItem("nickname", this.nickname);
          //  console.log(this.UserInfo);
         })
      },
      // 
      handleOpen(key, keyPath,index) {
        console.log(key, keyPath);
        console.log(index);
      },
      handleClose(key, keyPath,index) {
        console.log(key, keyPath);
        console.log(index);
      },
      Goto(){
        let but = document.querySelector('.el-submenu__title');
        but.click();
      }
    },
    computed:{
      isSignIn(){
        return this.$store.state.isSignIn;
      }
    },
    created(){
      this.GetInfo();
    }

  }
</script>

<style lang="scss" scoped>
.el-row {
  background-color:#2d2d2d;
  display: flex;
  align-items: center;
}
header {
  background-color:#2d2d2d;
  box-shadow: 1px 1px 1px 1px rgb(41, 40, 40);
  .logo {
  font-size: 18px;
  color: #fff;
  text-align: center;
  line-height: 60px;
  }
  .nav {
    float:right;
    li {
      padding: 0;
    }
    a {
      padding: 20px;
      .iconfont {
        color: #fff;
        // 
        vertical-align: top;
        margin: 0 5px 0 0 ;
      }
    }
    .login {
      background: rgb(19, 167, 226);
      color: #fff;
    }
  }
}

i {
  margin-right: 8px;
}


.el-menu.el-menu--horizontal {
   border-bottom: 0
}

</style>