<template>
  <div>
    <span>{{day}}{{hour}}{{minute}}{{seconds}}</span>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        day:null,
        hour:null,
        minute:null,
        seconds:null
      }
    },
    methods:{

    runtime(){// // ::
      const start = new Date("2021/11/20 0:00:00");
      let nowTime = new Date();
      let difference  = (nowTime.getTime()-start.getTime());
      let oneDaysecond = 24*60*60*1000;
      let d = difference/oneDaysecond;//  /   = 
       this.day  = Math.floor(d);//
      let h = (d-this.day)*24;// *  24  = 
       this.hour = Math.floor(h);
      let s = (h-this.hour)*60; // *  60  = 
       this.minute = Math.floor(s);
      let sec = (s - this.minute) * 60// *  60  = 
      this.seconds = Math.floor(sec);
     // span.innerHTML = ""+day+""+hour+""+minute+""+seconds+""
    }

    },
    created(){
      setInterval(this.runtime, 1000);
    }
  }
</script>

